:root {
  --primary-font:"Inter", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --light-blue: #2272FF;
  --dark-blue: #0D03C3;
  --dim-dark-blue: #040335;
  --light-blue-text: #0F0BC7;
  --icon-bg: #E3E2FD;
  --marquee-width: 90vw;
  --marquee-height: 20vh;
  /* --marquee-elements: 12; */ /* defined with JavaScript */
  --marquee-elements-displayed: 5;
  --marquee-element-width: calc(var(--marquee-width) / var(--marquee-elements-displayed));
  --marquee-animation-duration: calc(var(--marquee-elements) * 3s);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--primary-font);
  background-color: var(--white);
  overflow-x: hidden !important;
  scroll-behavior: smooth;
}

h1, h2, h3 {
  margin-top: 0;
  display: block;
}

a, a:visited, a:active {
  text-decoration: none;
}

button {
  cursor: pointer;
  outline: none;
  border: none;
  transition: all 400ms ease-in-out;
}

input:focus {
  border: none;
  outline: none;
}

.no-scroll {
  overflow: hidden;
}

.d-none {
  display: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none !important;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none !important;
  }
}

.padding-sides {
  padding-right: 27px;
  padding-left: 27px;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 0.3s ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 0.3s ease-in-out forwards;
}

@keyframes loopText {
  from {
    transform: translateX(0%);
  }
  to {
    transform: translateX(-100%);
  }
}
@keyframes fade-in-text {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }
}
.fade-in-text {
  animation: fade-in-text 800ms ease-in-out forwards;
}

.animate-text {
  transform: translateY(-15px);
  display: inline-block;
}

.heroSection a {
  color: #fff;
}
.heroSection i {
  cursor: pointer;
}
.heroSection svg {
  position: absolute;
  bottom: 0;
}
.heroSection .logo {
  cursor: pointer;
}
.heroSection button:hover {
  opacity: 0.8;
}
.heroSection__desktopNav--listItems li {
  transition: all 200ms ease-in-out;
  opacity: 0.8;
}
.heroSection__desktopNav--listItems li:hover {
  opacity: 1;
  transform: scaleY(20px);
}
.heroSection__mobileNav {
  position: absolute;
  z-index: 99;
  width: 90%;
  right: 0;
  left: 0;
  margin: 0 auto;
  border-radius: 8px;
  visibility: hidden;
  transition: all 1s ease-in-out;
  box-shadow: 2px 5px 203px -33px rgba(250, 239, 239, 0.75);
  -webkit-box-shadow: 2px 5px 203px -33px rgba(250, 239, 239, 0.75);
  -moz-box-shadow: 2px 5px 203px -33px rgba(250, 239, 239, 0.75);
  background: linear-gradient(108.21deg, #2272FF 0%, #0D03C3 97.89%);
}
.heroSection__mobileNav ul li {
  cursor: pointer;
  transition: all 200ms ease-in-out;
}
.heroSection__mobileNav ul li:not(:last-child) {
  opacity: 0.8;
}
.heroSection__mobileNav ul li:hover {
  opacity: 1;
  transform: scaleY(20px);
}
.heroSection__mobileNav ul li:last-child:hover {
  opacity: 0.8;
}
.heroSection__mobileNav ul li:last-child a {
  color: black;
}

.heroSection {
  position: relative;
  color: #fff;
  background: linear-gradient(108.21deg, #2272ff 0%, #0d03c3 97.89%);
  height: 140vh;
}
@media (max-width: 39.937em) {
  .heroSection {
    height: 600px;
  }
}
.heroSection__itemsContainer {
  margin-top: 1.25rem;
}
.heroSection__itemsContainer p {
  max-width: 28.125rem;
  margin: 1.25rem auto;
}
.heroSection__itemsContainer .inputCtn {
  max-width: 400px;
  text-align: center;
  position: relative !important;
  margin: 0 auto;
  z-index: 8;
}
.heroSection__itemsContainer .inputCtn i {
  position: absolute;
  left: 20px;
  top: 17px;
  color: black;
}
@media (max-width: 39.937em) {
  .heroSection__itemsContainer .inputCtn i {
    left: 17px;
    top: 15px;
  }
}
.heroSection__itemsContainer .inputCtn input {
  position: relative;
  padding: 1.25rem 0 1.25rem 3.75rem;
  border-radius: 50px;
  color: black !important;
  width: 100%;
}
@media (max-width: 39.937em) {
  .heroSection__itemsContainer .inputCtn input {
    padding-left: 50px;
    font-size: 0.8rem;
  }
}
.heroSection__itemsContainer .inputCtn button {
  position: absolute;
  right: 15px;
  top: 10px;
  background: linear-gradient(108.21deg, #2272ff 0%, #0d03c3 97.89%);
  border-radius: 50px;
}
@media (max-width: 39.937em) {
  .heroSection__itemsContainer .inputCtn button {
    font-size: 0.85rem;
    right: 7px;
  }
}
.heroSection .imgCtn {
  margin: 0 auto;
  text-align: center;
}
.heroSection .imgCtn img {
  text-align: center;
  margin: 0 auto;
  left: 0;
  right: 0;
  bottom: -6.875rem;
  width: 70%;
  position: absolute;
}
@media (max-width: 63.9375em) {
  .heroSection .imgCtn img {
    width: 110%;
  }
}

.ourTopCustomers {
  margin-bottom: 1.875rem;
}
.ourTopCustomers__container {
  white-space: nowrap;
  overflow: hidden;
}
@media (max-width: 63.9375em) {
  .ourTopCustomers__container {
    white-space: nowrap;
    /* .marquee-content:hover {
              animation-play-state: paused;
            } */
  }
  .ourTopCustomers__container .marquee {
    width: var(--marquee-width);
    height: var(--marquee-height);
    background-color: #111;
    color: #eee;
    overflow-x: hidden;
    position: relative;
  }
  .ourTopCustomers__container .marquee-content {
    list-style: none;
    height: 100%;
    display: flex;
    animation: scrolling var(--marquee-animation-duration) linear infinite;
  }
  @keyframes scrolling {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(calc(-1 * var(--marquee-element-width) * var(--marquee-elements)));
    }
  }
  .ourTopCustomers__container .marquee-content li {
    display: flex;
    justify-content: center;
    align-items: center;
    /* text-align: center; */
    flex-shrink: 0;
    width: var(--marquee-element-width);
    max-height: 100%;
    white-space: nowrap;
  }
  .ourTopCustomers__container .marquee-content li img {
    width: 100%;
    /* height: 100%; */
  }
}
@media (min-width: 64em) {
  .ourTopCustomers__container {
    width: 80%;
    margin: 0 auto;
  }
}

.services {
  padding-top: 3.4375rem;
  padding-bottom: 3.125rem;
}
.services__container {
  padding-top: 2.5rem;
}
.services__container .serviceContainer {
  background-color: white;
  text-align: center;
  width: 110%;
  max-width: 20.625rem;
  height: 22.8125rem;
  padding-top: 2.8125rem;
  border-radius: 20px;
  box-shadow: 0px 0px 100px rgba(0, 0, 0, 0.07);
}
.services__container .serviceContainer img {
  margin: 0 auto;
  background-color: var(--icon-bg);
  border-radius: 100%;
  padding: 0.875rem;
}
.services__container .serviceContainer h4 {
  margin: 25px;
}
.services__container .serviceContainer p {
  line-height: 25px;
  opacity: 0.8;
}
.services__container .serviceContainer .footer {
  margin-top: 2.1875rem;
  color: var(--light-blue-text);
  cursor: pointer;
}